@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/roboto-300.woff2") format("woff2"), url("../fonts/roboto-300.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-400.woff2") format("woff2"), url("../fonts/roboto-400.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-500.woff2") format("woff2"), url("../fonts/roboto-500.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-700.woff2") format("woff2"), url("../fonts/roboto-700.woff") format("woff");
}
@font-face {
  font-family: "Playfair Display";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/playfair-display-400.woff2") format("woff2"), url("../fonts/playfair-display-400.woff") format("woff");
}
@font-face {
  font-family: "Playfair Display";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/playfair-display-700.woff2") format("woff2"), url("../fonts/playfair-display-700.woff") format("woff");
}
@font-face {
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/prata-400.woff2") format("woff2"), url("../fonts/prata-400.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.centralizer {
  width: 1366px;
  max-width: 100%;
  margin: 0 auto;
  padding-left: clamp(20px, 7.2vw, 98px);
  padding-right: clamp(20px, 7.2vw, 98px);
}

.button {
  display: inline-block;
  max-width: 400px;
  margin: 0;
  padding: 26px 38px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 1.2;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  word-wrap: break-word;
  color: #ffffff;
  background-color: #444240;
  border: none;
  cursor: pointer;
}
.button:hover {
  opacity: 0.6;
}
.button:active {
  opacity: 0.8;
}
.button--secondary {
  background-color: #d4b098;
}
@media (max-width: 1199px) {
  .button {
    max-width: 100%;
    font-size: 14px;
  }
}

.socials {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 140px;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.socials__link:hover {
  color: #d4b098;
}
.socials__link:active {
  color: #5c5c50;
  opacity: 0.6;
}

.page {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  color: rgba(27, 26, 23, 0.82);
}

.page__main {
  flex-grow: 1;
}

.header {
  padding-top: 18px;
  padding-bottom: 18px;
  line-height: 1.2;
}
@media (max-width: 767px) {
  .header {
    position: relative;
    z-index: 2;
    min-height: 62px;
  }
}

.user-panel {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  max-width: 170px;
  color: #1b1a17;
}
@media (max-width: 767px) {
  .user-panel {
    display: block;
    background-color: rgba(212, 176, 152, 0.1);
  }
}

.user-panel__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px;
}
.user-panel__link:hover .user-panel__icon {
  color: #d4b098;
}
@media (max-width: 767px) {
  .user-panel__link {
    display: grid;
    grid-template-columns: 23px auto;
    column-gap: 15px;
    align-items: center;
    justify-items: end;
    padding: 25px 50px;
    padding-right: 85px;
    border-bottom: 1px solid #e6c8b4;
  }
}

@media (max-width: 767px) {
  .user-panel__text {
    position: static;
    width: fit-content;
    height: auto;
    text-transform: uppercase;
    color: rgba(27, 26, 23, 0.82);
    justify-self: start;
    order: 1;
  }
}

.user-panel__icon-container {
  position: relative;
}

.user-panel__icon {
  display: block;
}

.user-panel__added-count {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(5px);
  min-width: 13px;
  height: 13px;
  padding: 2px 4px;
  background-color: #e6c8b4;
  border-radius: 7px;
  font-size: 8px;
  text-align: center;
}

.main-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .main-navigation {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 18px 50px;
  }
}
@media (max-width: 767px) {
  .main-navigation {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: block;
    background-color: #fdfdfb;
  }
}

@media (max-width: 1199px) {
  .main-navigation__logo {
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .main-navigation__logo {
    display: block;
    width: fit-content;
    padding: 18px;
    margin: 0 auto;
  }
}

.main-navigation__button {
  display: none;
}
@media (max-width: 767px) {
  .main-navigation__button {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 62px;
    height: 62px;
    border: none;
    background: none;
    cursor: pointer;
  }
}

@media (max-width: 767px) {
  .main-navigation__button-inner {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 35px;
    height: 2px;
    background-color: rgba(27, 26, 23, 0.6);
    font-size: 0;
    color: transparent;
  }
  .main-navigation__button-inner::before, .main-navigation__button-inner::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 2px;
    background-color: inherit;
  }
  .main-navigation__button-inner::before {
    top: -8px;
  }
  .main-navigation__button-inner::after {
    bottom: -8px;
  }
  .main-navigation--mobile-opened .main-navigation__button-inner {
    visibility: hidden;
  }
  .main-navigation--mobile-opened .main-navigation__button-inner::before, .main-navigation--mobile-opened .main-navigation__button-inner::after {
    visibility: visible;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    margin: auto;
  }
  .main-navigation--mobile-opened .main-navigation__button-inner::before {
    transform: rotate(45deg);
  }
  .main-navigation--mobile-opened .main-navigation__button-inner::after {
    transform: rotate(-45deg);
  }
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  max-width: 700px;
  font-size: 14px;
}
@media (max-width: 1199px) {
  .main-navigation__list {
    position: relative;
    max-width: 100%;
    margin: 0 auto;
    grid-row: 2;
    grid-column: 1/-1;
  }
  .main-navigation__list::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    height: 1px;
    background-color: #e6c8b4;
    opacity: 0.5;
  }
}
@media (max-width: 767px) {
  .main-navigation__list {
    display: block;
    border-top: 1px solid #e6c8b4;
  }
}

.main-navigation__link {
  display: block;
  max-width: 140px;
  padding: 16px 32px;
  word-wrap: break-word;
}
.main-navigation__link:hover {
  color: #d4b098;
}
@media (max-width: 767px) {
  .main-navigation__link {
    display: none;
    max-width: 100%;
    padding: 25px 50px;
    font-size: 16px;
    text-align: center;
    text-transform: uppercase;
    border-bottom: 1px solid #e6c8b4;
  }
  .main-navigation--mobile-opened .main-navigation__link {
    display: block;
  }
}

.main-navigation__user-panel {
  margin-right: -12px;
}
@media (max-width: 1199px) {
  .main-navigation__user-panel {
    max-width: 100%;
    margin-left: auto;
  }
}
@media (max-width: 767px) {
  .main-navigation__user-panel {
    display: none;
    margin-right: 0;
  }
  .main-navigation--mobile-opened .main-navigation__user-panel {
    display: block;
  }
}

.footer {
  color: rgba(255, 255, 255, 0.75);
  font-size: 12px;
  line-height: 1.2;
  background-color: #444240;
}
@media (max-width: 1199px) {
  .footer {
    font-size: 16px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(165px, auto));
  gap: 50px;
  justify-content: space-between;
  padding: 73px 185px 67px;
}
@media (max-width: 1199px) {
  .footer__wrapper {
    padding: 50px 85px;
    grid-template-columns: repeat(auto-fit, minmax(210px, auto));
    justify-content: center;
  }
}

.footer__logo {
  display: block;
  margin-bottom: 30px;
}

.footer__text {
  display: block;
  margin-bottom: 15px;
}
.footer__text:last-child {
  margin-bottom: 0;
}

.links-group__title {
  max-width: 210px;
  margin: 0;
  margin-bottom: 33px;
  padding-top: 6px;
  font-size: 14px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .links-group__title {
    margin-bottom: 18px;
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .links-group__title {
    padding-top: 0;
  }
}

.links-group__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.links-group__item {
  margin-bottom: 15px;
}
.links-group__item:last-child {
