}

.banner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 768px;
  overflow: hidden;
}
@media (max-width: 1199px) {
  .banner {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    padding-right: 0;
  }
}

.banner__content {
  display: flex;
  padding-left: 85px;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 1199px) {
  .banner__content {
    margin: 0 auto;
    padding-left: clamp(20px, 7.2vw, 98px);
    padding-right: clamp(20px, 7.2vw, 98px);
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
