.banner__image-wrapper {
  position: relative;
  width: 506px;
  height: 768px;
  max-width: 100%;
  font-family: "Prata", "Times New Roman", serif;
  align-self: center;
}
.banner__image-wrapper::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-47%, -50%);
  z-index: -1;
  width: min(200vw, 990px);
  height: min(200vw, 944px);
  border-radius: 50%;
  background-image: linear-gradient(to right, transparent 500px, #ffffff 500px), linear-gradient(to top, #eae3d4, transparent);
}
@media (max-width: 1199px) {
  .banner__image-wrapper {
    height: fit-content;
  }
  .banner__image-wrapper::before {
    transform: translate(-50%, -50%);
    background-image: linear-gradient(to top, #eae3d4, transparent);
  }
}

.banner__image {
  position: absolute;
