}
.banner__price::before {
  content: "";
  position: absolute;
  top: 0.7em;
  right: -65px;
  width: 50px;
  height: 1px;
  background-color: #8b8371;
}
@media (max-width: 767px) {
  .banner__price {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.banner__product-description {
  bottom: 120px;
  right: -85px;
  z-index: 1;
  position: relative;
  position: absolute;
}
.banner__product-description::before {
  content: "";
  position: absolute;
  top: 0.7em;
  left: -65px;
  width: 50px;
  height: 1px;
  background-color: #8b8371;
}
@media (max-width: 767px) {
  .banner__product-description {
