.card {
  width: 370px;
  max-width: 100%;
  padding: 40px 28px;
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 14px;
  line-height: 1.7;
  background-color: #fdfdfb;
}
@media (max-width: 1199px) {
  .card {
    width: 100%;
    max-width: 313px;
    padding-left: 0;
    padding-right: 0;
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .card {
    position: relative;
  }
  .card::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    height: 1px;
    background-color: rgba(226, 208, 196, 0.5);
  }
  .card:last-child::after {
    display: none;
  }
}

.card__title {
  margin: 0;
  margin-bottom: 10px;
  padding: 0 12px;
  font-size: 1em;
}

.card__price {
  display: block;
  margin-bottom: 2px;
  padding: 0 12px;
  font-family: "Prata", "Times New Roman", serif;
}

.card__image-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 313px;
  max-width: 100%;
  margin: 0 auto 20px;
}
.card__image-link::before, .card__image-link::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  transition: all 0.4s ease-in-out;
}
.card__image-link::before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, transparent, #e2d0c4);
}
.card__image-link::after {
  width: calc(100% - 3px);
  height: calc(100% - 3px);
  background-color: #fdfdfb;
}
.card__image-link:hover::after {
  opacity: 0.6;
  width: 100%;
  height: 100%;
}
@media (max-width: 1199px) {
  .card__image-link::after {
    background-color: #ffffff;
  }
}

.card__image {
  position: relative;
  z-index: 1;
}

.card__link {
  padding: 0 12px;
  padding-left: 60px;
  position: relative;
  font-size: 16px;
  line-height: 1.33;
}
.card__link::before {
  content: "";
  position: absolute;
  top: 0.7em;
  left: 12px;
  width: 30px;
  height: 1px;
  background-color: #8b8371;
}

.about {
