.about__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 380px;
}
@media (max-width: 1199px) {
  .about__content {
    max-width: 600px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .about__content {
    margin: 0 auto;
    padding-left: clamp(20px, 7.2vw, 98px);
    padding-right: clamp(20px, 7.2vw, 98px);
  }
}

.about__title {
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 32px;
  line-height: 1.3;
  font-weight: 400;
  color: #1b1a17;
  margin: 0;
  margin-bottom: 38px;
}
@media (max-width: 767px) {
  .about__title {
    font-size: 28px;
  }
}
@media (max-width: 1199px) {
  .about__title {
    text-align: center;
  }
}

.about__text {
  margin: 0;
  margin-bottom: 15px;
  color: #444240;
}
@media (max-width: 1199px) {
  .about__text {
    text-align: center;
  }
}

.about__link {
  margin-top: 30px;
  margin-right: auto;
}
@media (max-width: 1199px) {
  .about__link {
    width: 400px;
    margin-top: 50px;
    margin-left: auto;
  }
}

.slider {
  position: relative;
  width: fit-content;
}
@media (max-width: 1199px) {
  .slider {
    padding: 0 65px;
    max-width: 100%;
  }
}
@media (max-width: 521px) {
  .slider {
    padding: 0;
  }
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  width: 521px;
  max-width: 100%;
  overflow: hidden;
}

.slider__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-shrink: 0;
}

.slider__controls {
  position: absolute;
  top: calc(100% + 10px);
  right: 75px;
  display: flex;
  align-items: center;
  color: #1b1a17;
}
@media (max-width: 1199px) {
  .slider__controls {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    justify-content: space-between;
  }
}

.slider__counter {
  font-family: "Prata", "Times New Roman", serif;
  font-size: 12px;
  line-height: 1.5;
  padding: 0 10px;
}
@media (max-width: 1199px) {
  .slider__counter {
    position: absolute;
    bottom: 0;
    right: 65px;
    padding: 15px 20px;
    font-size: 21px;
    background-color: rgba(255, 255, 255, 0.3);
  }
}
@media (max-width: 521px) {
  .slider__counter {
    right: 0;
  }
}

.slider__button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border: none;
  background: none;
  cursor: pointer;
}
.slider__button--right {
  transform: rotate(180deg);
}
.slider__button:hover {
  color: #d4b098;
}
.slider__button:active {
  color: #1b1a17;
  opacity: 0.4;
}
.slider__button:disabled {
  color: #1b1a17;
  opacity: 0.2;
  cursor: default;
}
@media (max-width: 1199px) {
  .slider__button {
    width: 65px;
    height: 65px;
  }
}

@media (max-width: 1199px) {
  .slider__arrow-icon {
    width: 30px;
    height: 30px;
  }
}

.subscribe {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 90px;
  color: #444240;
}
@media (max-width: 1199px) {
  .subscribe {
    display: block;
  }
}
@media (max-width: 767px) {
  .subscribe {
    display: grid;
    justify-content: stretch;
    padding-top: 40px;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
  }
}

.subscribe__content {
  max-width: 472px;
  margin-right: 70px;
  padding-top: 140px;
  padding-bottom: 50px;
}
@media (max-width: 1199px) {
  .subscribe__content {
    max-width: 630px;
    margin: 0 auto 25px;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .subscribe__content {
    margin: 0 auto;
    padding-left: clamp(20px, 7.2vw, 98px);
    padding-right: clamp(20px, 7.2vw, 98px);
    width: 100%;
    max-width: 100%;
    margin-bottom: 0;
    padding-top: 50px;
    padding-bottom: 50px;
    background-image: linear-gradient(to bottom, #ffffff 0, rgba(255, 255, 255, 0.8) 40%, rgba(255, 255, 255, 0.4));
    grid-row: 1;
    grid-column: 1;
    order: 1;
  }
}

.subscribe__title {
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 32px;
  line-height: 1.3;
  font-weight: 400;
  color: #1b1a17;
  margin: 0;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .subscribe__title {
    font-size: 28px;
  }
}

.subscribe__text {
  max-width: 376px;
  margin: 0;
  margin-bottom: 15px;
}

.subscribe__socials {
  margin-top: 90px;
  margin-left: -10px;
}
@media (max-width: 1199px) {
  .subscribe__socials {
    margin-top: 40px;
  }
}

.subscribe__gallery {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-auto-rows: min-content;
  grid-auto-flow: column dense;
  gap: 10px;
  min-width: fit-content;
  max-width: 630px;
  align-self: center;
}
@media (max-width: 1199px) {
  .subscribe__gallery {
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .subscribe__gallery {
    width: 100%;
    max-width: 100%;
    grid-row: 1;
    grid-column: 1;
  }
}
@media (max-width: 630px) {
  .subscribe__gallery {
    width: 100%;
    grid-auto-flow: row dense;
    gap: 0;
  }
}

.subscribe__image-container--large {
  grid-row: span 2;
}
@media (max-width: 630px) {
  .subscribe__image-container--large {
    grid-row: span 1;
    grid-column: span 2;
    order: -1;
  }
}

@media (max-width: 767px) {
  .subscribe__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
@media (max-width: 630px) {
  .subscribe__image {
    width: 100%;
    height: 100%;
    max-height: 400px;
    object-fit: cover;
  }
}